<template>
  <v-container style="max-width: 1400px; margin-top: 150px;">
    <h2 class="display-1 mb-5">
      Blogg
    </h2>

    <v-layout justify-end mb-5>
      <div style="max-width: 400px; display: flex; align-items: center;">
        <v-text-field
          label="Søk i blogg"
          append-icon="search"
          v-model="searchTerm"
          @keyup.enter="searchPosts"
        ></v-text-field>

        <v-btn depressed @click="searchPosts">
          søk
        </v-btn>
      </div>
    </v-layout>

    <v-layout justify-center>
      <div style="width: 100px;">
        <!-- <v-progress-linear
          v-if="fetching"
          color="#8ACB88"
          :indeterminate="true"
          height="5"
        ></v-progress-linear> -->
        <v-progress-circular
          v-if="fetching"
          :width="3"
          color="#8ACB88"
          indeterminate
        ></v-progress-circular>
      </div>
    </v-layout>

    <v-layout v-if="fetching === false">
      <v-flex>
        <v-layout wrap justify-center>
          <v-flex xs12 sm12 md6 lg4 xl4 mb-5 v-for="(post, i) in posts" :key="i">
            <v-card class="program-card ml-3 mr-3 mt-3">
              <div>
                <!-- Bilde her {{ post.featured_media }} -->
                <!-- aspect-ratio="1" -->

                <!-- :src="`https://picsum.photos/500/300?image=${i * 4}`"
                :lazy-src="`https://picsum.photos/10/6?image=${i * 4}`" -->
                <v-img
                  contain
                  :src="featuredMediaPath(post)"
                  class="grey lighten-2"
                >
                  <template v-slot:placeholder>
                    <v-layout
                      fill-height
                      align-center
                      justify-center
                      ma-0
                    >
                      <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                    </v-layout>
                  </template>
                </v-img>
              </div>

              <div class="title-wrapper">
                <v-card-title primary-title class="pl-4 pr-4">
                  <div>
                    <h3 class="headline" v-html="post.title.rendered"></h3>
                    <!-- <h3 class="subheading" v-html="post.date"></h3> -->
                  </div>
                </v-card-title>

                <v-card-text>
                  <div v-html="post.excerpt.rendered"></div>
                </v-card-text>
              </div>

              <v-card-actions class="justify-center">
                <div class="mb-1">
                  <router-link class="button submit" tag="button" :to="'/innlegg/' + post.id">
                    <span>Les innlegg</span>
                  </router-link>
                </div>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>

      <v-flex
        xs12 sm12 md6 lg4 xl4
        mb-5 mt-2
        pt-3 pb-3 pl-5
      >
        <h4 class="pl-1">Kategorier</h4>

        <div v-for="(category, i) in categories" :key="i" class="category">
          <router-link :to="'/blogg/kategori/' + category.id" style="color: black;">
            {{ category.name }}
          </router-link>
        </div>
      </v-flex>
    </v-layout>

    <v-layout v-if="fetching === false" justify-center mt-5>
      <v-btn v-if="everythingLoaded === false" outline large class="text-none" @click="fetchPosts">
        Last flere innlegg
        <v-icon right>expand_more</v-icon>
      </v-btn>
      <h2 v-else class="subheading">
        Alle innlegg er lasted inn :)
      </h2>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      posts: [],
      searchTerm: '',
      categories: [],
      fetching: false,
      fetchingMore: false,
      page: 0,
      everythingLoaded: false
    }
  },

  created () {
    this.fetchPosts()
    this.fetchCategories()
  },

  methods: {
    featuredMediaPath (post) {
      if (post._embedded.hasOwnProperty(['wp:featuredmedia'])) {
        return post._embedded['wp:featuredmedia']['0'].source_url
      } else {
        return ''
      }
    },

    fetchPosts () {
      if (this.page === 0) {
        this.fetching = true
      }

      const perPage = 8
      let currentPage = ++this.page

      this.$axios.get('/posts?_embed&per_page=' + perPage + '&page=' + currentPage).then((items) => {
        console.log(items.data)
        this.posts.push(...items.data)

        // if we do not receive as many posts as requested we've fetch all posts
        if (items.data.length < perPage) this.everythingLoaded = true

        this.fetching = false
      }).catch((error) => {
        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx
          console.log(error.response.data.code)
          console.log(error.response.status)
          console.log(error.response.headers)

          if (error.response.data.code === 'rest_post_invalid_page_number') this.everythingLoaded = true
        } else if (error.request) {
          // The request was made but no response was received
          // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
          // http.ClientRequest in node.js
          console.log(error.request)
        } else {
          // Something happened in setting up the request that triggered an Error
          console.log('Error', error.message)
        }

        // console.log(error.config)
        this.fetching = false
      })
    },

    fetchCategories () {
      this.$axios.get('/categories').then((items) => {
        console.log(items.data)
        this.categories = items.data
      })
    },

    searchPosts () {
      this.$axios.get('/posts?search=' + this.searchTerm).then((items) => {
        console.log(items.data)
      })
    }
  }
}
</script>

<style scoped lang="css">
  .program-card {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .title-wrapper {
    flex-grow: 1;
  }

  .categories {
    display: flex;
    flex-wrap: wrap;
  }

  .category {
    /*background: #8ACB88;
    color: #fff;*/
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 15px;
    margin: 5px;
    cursor: pointer;
    /*font-weight: 200;*/
  }
</style>
